import React from 'react';
import styles from './TeamPane.module.scss';
import { people, personalPageProps } from '../../../../data';

const Profile = ({ profile }) => {
  return (
    <div className={styles.profile}>
      <div className={styles.profileImage}>
        <img src={profile.photo} alt="profile" />
        <div>
          {personalPageProps.map(props => {
            if (profile[props.key]) {
              return (
                <a
                  href={profile[props.key]}
                  target="_blank"
                  rel="noopener noreferrer"
                  key={props.key}
                >
                  <props.icon />
                </a>
              );
            }
            return null;
          })}
        </div>
      </div>
      <div className={styles['font-size-24-noto']}>
        {profile.firstName} {profile.lastName}
      </div>
      <div className={styles['font-size-16-noto']}>{profile.role}</div>
    </div>
  );
};

const TeamPane = () => {
  return (
    <div className={[styles.root, 'ga-track'].join(' ')} data-name="team">
      <div className={styles.tag}>MEET THE PEOPLE</div>
      <div className={styles['font-size-40']}>Meet the Dream Team</div>
      <div className={styles['font-size-20']}>
        A team of engineers, expert researchers, and problem solvers <br />
        set out to change the way machines understand videos
      </div>
      <div className={styles.grid}>
        {people.map((profile, idx) => (
          <Profile profile={profile} key={idx} />
        ))}
      </div>
    </div>
  );
};

export default TeamPane;
