import React from 'react';
import styles from './ArrowAnchor.module.scss';
import { ReactComponent as RightArrowButton } from '../../assets/right-arrow-button.svg';

const ArrowAnchor = ({ color, href, text }) => {
  return (
    <a
      target="_blank"
      rel="noopener noreferrer"
      href={href}
      style={{ color: color }}
      className={styles.root}
    >
      <span>{text}</span>
      <RightArrowButton />
    </a>
  );
};

ArrowAnchor.defaultProps = {
  color: 'white',
  href: '/',
  text: '',
};

export default ArrowAnchor;
